import React, { useEffect, useState } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { connect } from "react-redux";
import { courseStoreInterface, loadCourses } from "../../../actions";
import Loading from "../../../components/Loading/Loading";
import { course } from "../../../interfaces/course";
import { StoreState } from "../../../reducers";

interface SchoolLevelsPropsInterface {
  thisCourses: course[] | null;
  thisCoursesId: string[] | null;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
  };
  setThisCourses: React.Dispatch<React.SetStateAction<course[] | null>>;
  setThisCoursesId: React.Dispatch<React.SetStateAction<string[] | null>>;
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
    }>
  >;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
}

const StepTwo = (props: SchoolLevelsPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(true);

  const changeCourseList = (course: course) => {
    if (props.thisCourses && props.thisCoursesId) {
      if (
        props.thisCourses.find((item) => item.course_id === course.course_id)
      ) {
        let newArray = [...props.thisCourses];
        let newArrayId = [...props.thisCoursesId];
        newArray = newArray.filter(
          (item) => item.course_id !== course.course_id
        );
        newArrayId = newArrayId.filter((item) => item !== course.course_id);
        props.setThisCourses(newArray);
        props.setThisCoursesId(newArrayId);
      } else {
        props.setThisCourses([...props.thisCourses, course]);
        props.setThisCoursesId([...props.thisCoursesId, course.course_id]);
      }
    } else {
      props.setThisCourses([course]);
      props.setThisCoursesId([course.course_id]);
    }
  };

  useEffect(() => {
    if (props.course.courses === null) {
      props.loadCourses(setLoading);
    } else {
      setLoading(false);
    }
  }, [props, setLoading]);

  if (loading) {
    return <Loading />;
  }
  return (
    <div>
      {props.course.courses && props.course.courses.length > 0 ? (
        <section className="m-1">
          <div className="text-lg ml-1 font-semibold text-gray-600">
            Choose courses:
          </div>
          {props.course.courses.map((item, index) => (
            <div
              key={index + item.course_id}
              className={`px-3 py-2 my-1 border rounded hover:bg-blue-50 cursor-pointer flex items-center gap-2 text-lg ${
                props.thisCourses &&
                props.thisCourses.find(
                  (course) => course.course_id === item.course_id
                ) &&
                "bg-blue-50 text-blue-700 font-bold border-blue-700"
              }`}
              onClick={() => changeCourseList(item)}
            >
              {props.thisCourses &&
              props.thisCourses.find(
                (course) => course.course_id === item.course_id
              ) ? (
                <ImCheckboxChecked className="w-8 h-8" />
              ) : (
                <ImCheckboxUnchecked className="w-8 h-8" />
              )}
              <div>
                <div className="text-lg">{item.course_name}</div>
                <div className="text-base">({item.course_code})</div>
              </div>
            </div>
          ))}
        </section>
      ) : (
        <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
          <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
          <span>No Courses available</span>
        </div>
      )}
      <div className="flex items-center justify-between">
        <button
          className="btn bg-gray-500 text-gray-50 float-right m-1 mr-2 mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() =>
            props.setFormSteps({ ONE: true, TWO: false, THREE: false })
          }
        >
          Back
        </button>
        {props.thisCourses && props.thisCourses.length > 0 && (
          <button
            className="btn btn-blue float-right m-1 mr-2 mb-2 animate__animated animate__fadeIn focus:outline-none"
            onClick={() =>
              props.setFormSteps({ ONE: false, TWO: false, THREE: true })
            }
          >
            Continue
          </button>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = ({
  course,
}: StoreState): {
  course: courseStoreInterface;
} => {
  return {
    course: course,
  };
};

export default connect(mapStateToProps, {
  loadCourses,
})(StepTwo);
