import React, { Fragment, useEffect, useState } from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { IoDocumentOutline } from "react-icons/io5";
import {
  MdBrightness1,
  MdClose,
  MdErrorOutline,
  MdInfo,
  MdRemoveCircleOutline,
} from "react-icons/md";
import { ImFilesEmpty } from "react-icons/im";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../actions/document";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  CandidatesInterface,
  CandidateDetailsInterface,
} from "../../shared/interfaces";
import { countByStatus } from "./DocumentChecking";
import { PopupAlertData } from "../../components/Alert/Popup";

interface AppProps {
  documentCategories: DocumentReducerInterface;
  candidate: CandidatesInterface;
  candidateCheckData: CandidatesInterface | null;
  filterByCategoryId: string;
  filterBySubCategoryId: string;
  setCandidateCheckData: React.Dispatch<
    React.SetStateAction<CandidatesInterface | null>
  >;
  setDoc: React.Dispatch<
    React.SetStateAction<CandidateDetailsInterface | null>
  >;
  setDocTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocSubTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocIndex: React.Dispatch<React.SetStateAction<number>>;
  getDocumentFields: Function;
}
const UserDocuments: React.FC<AppProps> = ({
  candidate,
  documentCategories,
  candidateCheckData,
  filterByCategoryId,
  filterBySubCategoryId,
  setCandidateCheckData,
  setDoc,
  setDocTitle,
  setDocSubTitle,
  setDocFieldTitle,
  setDocIndex,
  getDocumentFields,
}) => {
  !candidateCheckData && setCandidateCheckData(candidate);
  const [tabsController, setTabsController] = useState<
    "waiting" | "approved" | "rejected"
  >("waiting");
  const [filterByCategory, setFilterByCategory] =
    useState<string>(filterByCategoryId);
  const [filterBySubCategory, setFilterBySubCategory] = useState<string>(
    filterBySubCategoryId
  );

  useEffect(() => {
    if (!documentCategories.documentFields) {
      getDocumentFields();
    }
  }, [documentCategories.documentFields, getDocumentFields]);

  const countById = (
    userDocs: CandidateDetailsInterface[],
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ): number => {
    console.log({ userDocs });
    let result: CandidateDetailsInterface[] = userDocs;
    // result = userDocs.filter(
    //   (details) => details.validation_status === docStatus && details.files
    // );
    if (filterType === "docCategory") {
      result = userDocs.filter(
        (details) =>
          details.category_id === filterValue && details.files.length > 0
      );
    } else if (filterType === "docSubCategory") {
      result = userDocs.filter(
        (details) =>
          details.sub_category_id === filterValue && details.files.length > 0
      );
    }
    // if (docStatus === "approved") {
    //   result = userDocs.filter(
    //     (details) => details.validation_status !== "waiting"
    //   );
    // }
    return result.length;
  };

  const filterData = (
    filterType: "docCategory" | "docSubCategory" | "docStatus",
    filterValue: string
  ) => {
    let result: CandidateDetailsInterface[];
    setCandidateCheckData(candidate);
    if (candidateCheckData) {
      result = candidateCheckData.details;
    } else {
      result = candidate.details;
    }

    // filter by category
    if (filterType === "docCategory") {
      result = candidate.details.filter(
        (detail) => detail.category_id === filterValue
      );
    }

    // filter by sub category
    if (filterType === "docSubCategory") {
      result = candidate.details.filter(
        (detail) => detail.sub_category_id === filterValue
      );
    }

    setCandidateCheckData({
      user_id: candidate.user_id,
      n_id: candidate.n_id ? candidate.n_id : "",
      fname: candidate.fname,
      lname: candidate.lname,
      district: candidate.district,
      phone_number: candidate.phone_number,
      email: candidate.email,
      details: result,
    });
  };

  return (
    <div className="w-full flex bg-gray-100 h-full flex-col md:flex-row md:items-start">
      {/* left side for filter */}
      <section className="bg-white h-full w-1/6 p-2 m-3 border border-gray-200 rounded">
        {documentCategories.documentCategory &&
          documentCategories.documentCategory.map((category, index) => (
            <Fragment key={index + category.user_detail_category_id}>
              {countById(
                candidate.details,
                "docCategory",
                category.user_detail_category_id
              ) > 0 && (
                <section>
                  <section
                    className={`${
                      filterByCategory === category.user_detail_category_id
                        ? "bg-blue-500 text-white font-bold"
                        : "bg-blue-100 text-blue-800"
                    } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex cursor-pointer items-center animate__animated animate__zoomIn`}
                    onClick={() => {
                      setFilterByCategory(category.user_detail_category_id);
                      setFilterBySubCategory("");
                      filterData(
                        "docCategory",
                        category.user_detail_category_id
                      );
                    }}
                  >
                    <section className="flex-1">
                      <MdBrightness1 className="inline-block mr-1 text-xs" />
                      <span>{category.title}</span>
                    </section>

                    {/* {count > 0 ? ( */}
                    <span className="px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full">
                      {countById(
                        candidate.details,
                        "docCategory",
                        category.user_detail_category_id
                      )}
                    </span>
                    {/* ) : null} */}
                  </section>

                  {/* sub filter */}
                  {filterByCategory === category.user_detail_category_id &&
                    documentCategories.documentSubCategory &&
                    documentCategories.documentSubCategory
                      .filter(
                        (item) =>
                          item.category_id === category.user_detail_category_id
                      )
                      .map(
                        (subCategory, i) =>
                          countById(
                            candidate.details,
                            "docSubCategory",
                            subCategory.user_detail_subcategory_id
                          ) > 0 && (
                            <div
                              key={i + subCategory.user_detail_subcategory_id}
                              className="ml-3 border-l-2 border-blue-200 -mt-1 pt-2 mb-2 pl-1"
                            >
                              <div
                                className={`animate__animated animate__zoomIn animate__faster ${
                                  filterBySubCategory ===
                                  subCategory.user_detail_subcategory_id
                                    ? "bg-blue-500 text-white font-bold"
                                    : " text-blue-800"
                                } bg-blue-200 rounded-md px-1 mb-1 flex items-center py-1 font-bold`}
                                onClick={() => {
                                  setFilterBySubCategory(
                                    subCategory.user_detail_subcategory_id
                                  );
                                  filterData(
                                    "docSubCategory",
                                    subCategory.user_detail_subcategory_id
                                  );
                                }}
                              >
                                {/* <MdBrightness1 className="inline-block mr-0.1 text-xs" />
                            <span className="inline-block pl-1">Sub Title</span> */}
                                <section className="flex-1">
                                  <MdBrightness1
                                    className={`inline-block mr-0.1 text-xs ${
                                      filterBySubCategory ===
                                      subCategory.user_detail_subcategory_id
                                        ? "text-white"
                                        : ""
                                    }`}
                                  />
                                  <span>{subCategory.title}</span>
                                </section>
                                <span
                                  className={`px-0.5 my-1 text-xs bg-blue-600 text-white rounded-full`}
                                >
                                  {countById(
                                    candidate.details,
                                    "docSubCategory",
                                    subCategory.user_detail_subcategory_id
                                  )}
                                </span>
                              </div>
                            </div>
                          )
                      )}

                  {/* end sub filter */}
                </section>
              )}
            </Fragment>
          ))}
      </section>
      {/* end left side for filter */}

      {/* rigth side */}
      <section className="w-5/6 h-full my-3 mr-3">
        {/* tabs status */}
        <div className="w-full bg-white flex items-center justify-evenly border border-gray-200 rounded">
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("waiting")}
          >
            <div
              className={`flex items-center py-4 px-2 text-yellow-800 gap-1 ${
                tabsController === "waiting" && "border-b-4 border-yellow-800"
              } animate__animated animate__pulse`}
            >
              <MdInfo className="w-6 h-6" />
              <span>Pending</span>
              <span>
                (
                {candidateCheckData &&
                  countByStatus(candidateCheckData.details, "waiting")}
                )
              </span>
            </div>
          </section>
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("rejected")}
          >
            <div
              className={`flex items-center py-4 px-2 text-gray-700 gap-1 ${
                tabsController === "rejected" && "border-b-4 border-gray-700"
              } animate__animated animate__pulse`}
            >
              <MdRemoveCircleOutline className="w-6 h-6" />
              <span>Rejected</span>
              <span>
                (
                {candidateCheckData &&
                  countByStatus(candidateCheckData.details, "rejected")}
                )
              </span>
            </div>
          </section>
          <section
            className="cursor-pointer"
            onClick={() => setTabsController("approved")}
          >
            <div
              className={`flex items-center py-4 px-2 text-blue-700 gap-1 ${
                tabsController === "approved" && " border-b-4 border-blue-700"
              } animate__animated animate__pulse`}
            >
              <FaRegCheckCircle className="w-6 h-6" />
              <span>Approved</span>
              <span>
                (
                {candidateCheckData &&
                  countByStatus(candidateCheckData.details, "approved")}
                )
              </span>
            </div>
          </section>
        </div>
        {/* end tabs status */}

        {/* list of docs */}
        <section className="w-full h-full my-3">
          {/* doc card */}
          {candidateCheckData &&
          candidateCheckData.details.filter(
            (itm) =>
              itm.validation_status === tabsController && itm.files.length > 0
          ).length > 0 ? (
            candidateCheckData.details
              .filter(
                (itm) =>
                  itm.validation_status === tabsController &&
                  itm.files.length > 0
              )
              .map((item, index) => (
                <div
                  key={index + item.doc_id}
                  className="bg-white border border-gray-200 rounded mb-3 p-4 flex justify-between items-start animate__animated animate__zoomIn"
                >
                  {/* doc title */}
                  <div>
                    <h2 className="text-balck font-semibold text-xl">
                      {item.sub_category_id
                        ? documentCategories.documentSubCategory &&
                          documentCategories.documentSubCategory.filter(
                            (c) =>
                              c.user_detail_subcategory_id ===
                              item.sub_category_id
                          )[0].title
                        : item.category_id
                        ? documentCategories.documentCategory &&
                          documentCategories.documentCategory.filter(
                            (doc) =>
                              doc.user_detail_category_id === item.category_id
                          )[0].title
                        : item.title
                        ? item.title
                        : "No Category or Title Available"}
                    </h2>
                    {/* status */}
                    <div className="flex items-center gap-2">
                      <span className="text-gray-500 italic">Status:</span>
                      <span
                        className={`text-${
                          item.validation_status === "waiting"
                            ? "yellow"
                            : item.validation_status === "approved"
                            ? "blue"
                            : "gray"
                        }-800`}
                      >
                        {item.validation_status === "waiting"
                          ? "Pending"
                          : item.validation_status === "approved"
                          ? "Approved"
                          : "Rejected"}
                      </span>
                    </div>
                    {/* end status */}
                  </div>
                  {/* end doc title */}
                  {console.log({ item })}
                  {console.log({
                    fields: documentCategories.documentFields,
                    fieldId: item.category_subcategory_data,
                  })}
                  <button
                    className="bg-blue-600 p-2 text-white flex rounded items-center space-x-2 h-max focus:outline-none"
                    onClick={() => {
                      setDoc(item);
                      setDocTitle(
                        item.category_id
                          ? documentCategories.documentCategory
                            ? documentCategories.documentCategory.filter(
                                (doc) =>
                                  doc.user_detail_category_id ===
                                  item.category_id
                              )[0].title
                            : item.title
                            ? item.title
                            : "No Category or Title Available"
                          : "No Category or Title Available"
                      );
                      setDocSubTitle(
                        item.sub_category_id
                          ? documentCategories.documentSubCategory
                            ? documentCategories.documentSubCategory.filter(
                                (c) =>
                                  c.user_detail_subcategory_id ===
                                  item.sub_category_id
                              )[0].title
                            : ""
                          : ""
                      );
                      console.log({
                        fields: documentCategories.documentFields,
                        fieldId: item.category_subcategory_data,
                      });
                      setDocFieldTitle(
                        item.category_subcategory_data
                          ? documentCategories.documentFields
                            ? documentCategories.documentFields.filter(
                                (c) => c._id === item.category_subcategory_data
                              ).length > 0
                              ? documentCategories.documentFields.filter(
                                  (c) =>
                                    c._id === item.category_subcategory_data
                                )[0].title
                              : ""
                            : ""
                          : ""
                      );
                      setDocIndex(index);
                    }}
                  >
                    <IoDocumentOutline className="w-5 h-5" />
                    View doc
                  </button>
                </div>
              ))
          ) : (
            <div className="bg-white border border-gray-200 rounded mb-3 p-4 text-gray-400 text-center animate__animated animate__pulse">
              <ImFilesEmpty className="w-28 h-28 mx-auto mb-3" />
              <span className="text-2xl font-mono font-semibold tracking-tighter">
                No Data Found
              </span>
            </div>
          )}

          {/* end doc card */}
        </section>
        {/* end list of docs */}
      </section>
      {/* end rigth side */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
})(UserDocuments);
