import React from "react";
import Table from "../../../components/Table/Table";
import { PositionSubcategoryData } from "../../../interfaces/position";

interface RequirementProps {
  requirement: PositionSubcategoryData[];
}

const ViewRequirements = (props: RequirementProps) => {
  const tableData = (): JSX.Element[] => {
    let data: PositionSubcategoryData[] = props.requirement;
    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={3}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((item, index) => (
      <tr key={index} className="border-b border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">
          <span>{item.title}</span>
        </td>
        <td className="p-3 text-left">
          {item.required ? "Required" : "Not required"}
        </td>
      </tr>
    ));
  };

  return (
    <div>
      <Table
        columns={[
          { columnTitle: "#", alignText: "left", padding: "p-3" },
          { columnTitle: "Document", alignText: "left", padding: "p-3" },
          { columnTitle: "Status", alignText: "left", padding: "p-3" },
          // { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={false}
        thColor="gray"
      />
    </div>
  );
};

export default ViewRequirements;
