import React, { Fragment, useEffect, useState } from "react";
import axios from "axios";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { MdDelete, MdEdit, MdExpandLess, MdExpandMore } from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../../actions";
import { deletePosition, getPositions } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { education_category } from "../../../interfaces/education_category";
import { school_level } from "../../../interfaces/school_level";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import {
  PositionAPI,
  PositionRequirements,
} from "../../../interfaces/position";
import { StoreState } from "../../../reducers";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import StepOne from "./StepOne";
import ViewPosition from "./ViewPosition";
import StepTwo from "./StepTwo";
import { course } from "../../../interfaces/course";
import StepThree from "./StepThree";

export interface positionRequirementInterface {
  category_subcategory_data_id: string;
  required: boolean;
}
interface AppPropsInterface {
  systemPositions: PositionAPI[];
  getPositions: Function;
  deletePosition: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Index = (props: AppPropsInterface) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [isDeleting, setIsDeleting] = useState<string>("");
  const [thisPosition, setThisPosition] = useState<PositionAPI | null>(null);
  const [modalAction, setModalAction] = useState<
    "CREATE_POSITION" | "UPDATE_POSITION" | false
  >(false);
  const [formSteps, setFormSteps] = useState<{
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
  }>({ ONE: false, TWO: false, THREE: false });
  const [thisEdication, setThisEdication] = useState<education_category | null>(
    null
  );
  const [thisLevel, setThisLevel] = useState<school_level | null>(null);
  const [thisCourses, setThisCourses] = useState<course[] | null>(null);
  const [thisCoursesId, setThisCoursesId] = useState<string[] | null>(null);
  const [positionId, setPositionId] = useState<string>("");
  const [positionTitle, setPositionTitle] = useState<string>("");
  const [positionDesc, setPositionDesc] = useState<string>("");
  const [thisCategory, setThisCategory] = useState<string>("");
  const [thisSubCategory, setThisSubCategory] = useState<string>("");
  const [thisFieldList, setThisFieldList] = useState<
    positionRequirementInterface[] | null
  >(null);
  const [thisRequirementList, setThisRequirementList] = useState<
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null
  >(null);

  useEffect(() => {
    if (props.systemPositions === null || props.systemPositions.length <= 0) {
      props.getPositions((status: boolean, msg: string) => {
        setLoading(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }
  }, [props, setLoading]);

  const handleDelete = async (position_id: string) => {
    if (window.confirm("Are you sure do you want to delete this position?")) {
      setIsDeleting(position_id);
      setLoadingRequest(true);
      try {
        const res = await axios.delete(
          `${API.APPLICATION}/position/${position_id}`
        );

        if (res.data) {
          console.log({ res });
          props.deletePosition(position_id);
          props.PushPopupAlert({
            title: "Position deleted successfully",
            theme: PopupAlertTheme.success,
          });
        }
      } catch (error) {
        console.log({ position_error: error });
        props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setIsDeleting("");
      setLoadingRequest(false);
    }
  };

  const cleanStates = () => {
    setThisEdication(null);
    setThisLevel(null);
    setThisCourses(null);
    setThisCoursesId(null);
    setPositionTitle("");
    setPositionId("");
    setPositionDesc("");
    setThisFieldList(null);
    setThisRequirementList(null);
    setFormSteps({ ONE: false, TWO: false, THREE: false });
  };

  const assignRequirementsUpdate = (requirements: PositionRequirements[]) => {
    let newArray: {
      user_detail_category_id: string;
      user_detail_subcategory_id: string;
      position_requirement: positionRequirementInterface[];
      required: boolean;
    }[] = [];
    let newRequirement: positionRequirementInterface[] = [];
    requirements.forEach((element) => {
      element.position_requirement.forEach((item) => {
        newRequirement.push({
          category_subcategory_data_id: item._id,
          required: item.required,
        });
      });
      newArray.push({
        user_detail_category_id: element.category.user_detail_category_id,
        user_detail_subcategory_id:
          element.subcategory.user_detail_subcategory_id,
        position_requirement: newRequirement,
        required: element.required,
      });
    });

    return newArray;
  };

  if (props.systemPositions === null || loading === true) {
    <Loading />;
  }

  return (
    <Fragment>
      <div className="mt-1">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <div className="text-blue-600 font-extrabold text-xl px-2">
            Positions
          </div>
          {props.systemPositions !== null && (
            <div
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
              //   dsabled={loadingRequest}
              onClick={() => {
                setModalAction("CREATE_POSITION");
                setFormSteps({ ONE: true, TWO: false, THREE: false });
              }}
            >
              Add new
            </div>
          )}
        </div>
        <div className="flex flex-col">
          {/* {console.log({ postions: props.systemPositions })} */}
          {props.systemPositions.length === 0 ? (
            <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
              <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
              <span>No position available</span>
            </div>
          ) : (
            props.systemPositions.map((item, i) => (
              <div key={i + item.position_id} className={`border`}>
                <div
                  className={`w-full flex flex-row px-3 py-2 rounded items-start my-1 justify-between cursor-pointer ${
                    thisPosition &&
                    thisPosition.position_id === item.position_id
                      ? "bg-blue-100 border border-blue-500"
                      : "hover:bg-gray-100"
                  }`}
                >
                  <div className="flex flex-col">
                    <span className="text-gray-600 font-bold">
                      {item.position_title}
                    </span>
                    <span className={`text-xs pr-5 text-gray-500`}>
                      {item.description}
                    </span>
                  </div>
                  <div className="flex flex-row items-center gap-2">
                    {thisPosition &&
                      thisPosition.position_id === item.position_id && (
                        <div className="flex items-center gap-2">
                          <button
                            className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                              loadingRequest
                                ? "cursor-not-allowed"
                                : "cursor-pointer"
                            } animate__animated animate__fadeIn`}
                            disabled={loadingRequest}
                            onClick={() => {
                              setThisEdication(item.education_institute_id);
                              setThisLevel(item.school_level_id);
                              setThisCourses(item.courses);
                              setThisCoursesId(
                                item.courses.map((item) => item.course_id)
                              );
                              setPositionTitle(item.position_title);
                              setPositionId(item.position_id);
                              setPositionDesc(item.description);
                              setThisRequirementList(
                                assignRequirementsUpdate(item.requirements)
                              );
                              setFormSteps({
                                ONE: true,
                                TWO: false,
                                THREE: false,
                              });
                              setModalAction("UPDATE_POSITION");
                            }}
                          >
                            <MdEdit className="text-3xl text-blue-700" />
                          </button>
                          <button
                            className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                              loadingRequest
                                ? "cursor-not-allowed"
                                : "cursor-pointer"
                            } animate__animated animate__fadeIn`}
                            onClick={() => handleDelete(item.position_id)}
                          >
                            <MdDelete className="text-3xl text-red-700" />
                          </button>
                        </div>
                      )}
                    <div
                      onClick={() => {
                        !loadingRequest &&
                        thisPosition &&
                        thisPosition.position_id === item.position_id
                          ? setThisPosition(null)
                          : setThisPosition(item);
                      }}
                      className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 ${
                        loadingRequest ? "cursor-not-allowed" : "cursor-pointer"
                      }`}
                    >
                      {thisPosition &&
                      thisPosition.position_id === item.position_id ? (
                        <MdExpandLess className="text-3xl text-gray-700" />
                      ) : (
                        <MdExpandMore className="text-3xl text-gray-700" />
                      )}
                    </div>
                  </div>
                </div>
                {thisPosition &&
                  thisPosition.position_id === item.position_id && (
                    <ViewPosition thisPosition={thisPosition} />
                  )}
              </div>
            ))
          )}
        </div>
      </div>

      {/* MODAL CONTROLLER */}
      {modalAction && (
        <Modal
          title={
            <div className="flex flex-row gap-4 items-center">
              <div className="flex flex-col">
                <span className="font-extrabold text-2xl text-gray-500">
                  {formSteps.ONE && "Choose Education Institute & School Level"}
                </span>
                <span className="text-base text-gray-500 font-normal">
                  {modalAction === "UPDATE_POSITION"
                    ? "Updating the selected position"
                    : "Creating a new position"}
                </span>
              </div>
            </div>
          }
          backDrop={true}
          theme={Themes.default}
          close={() => {
            setModalAction(false);
            cleanStates();
          }}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          backDropClose={false}
          padding={{ title: true, body: false }}
        >
          {formSteps.ONE && (
            <StepOne
              thisLevel={thisLevel}
              thisEdication={thisEdication}
              formSteps={formSteps}
              setThisEdication={setThisEdication}
              setThisLevel={setThisLevel}
              setFormSteps={setFormSteps}
            />
          )}
          {formSteps.TWO && (
            <StepTwo
              thisCourses={thisCourses}
              thisCoursesId={thisCoursesId}
              formSteps={formSteps}
              setThisCourses={setThisCourses}
              setThisCoursesId={setThisCoursesId}
              setFormSteps={setFormSteps}
            />
          )}
          {formSteps.THREE && thisEdication && thisLevel && thisCoursesId && (
            <StepThree
              positionId={positionId}
              positionDesc={positionDesc}
              positionTitle={positionTitle}
              thisFieldList={thisFieldList}
              thisRequirementList={thisRequirementList}
              thisEdication={thisEdication}
              thisLevel={thisLevel}
              thisCategory={thisCategory}
              thisSubCategory={thisSubCategory}
              thisCoursesId={thisCoursesId}
              formSteps={formSteps}
              modalAction={modalAction}
              setPositionId={setPositionId}
              setModalAction={setModalAction}
              setThisFieldList={setThisFieldList}
              setPositionDesc={setPositionDesc}
              setPositionTitle={setPositionTitle}
              setThisCategory={setThisCategory}
              setThisSubCategory={setThisSubCategory}
              setThisRequirementList={setThisRequirementList}
              setThisCourses={setThisCourses}
              setFormSteps={setFormSteps}
              cleanStates={cleanStates}
            />
          )}
        </Modal>
      )}
      {/* MODAL CONTROLLER */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
}: StoreState): {
  auth: Auth;
  systemPositions: PositionAPI[];
} => {
  return {
    auth: auth,
    systemPositions: system_settings.systemPositions,
  };
};

export default connect(mapStateToProps, {
  getPositions,
  deletePosition,
  PushPopupAlert,
})(Index);
