import { ActionTypes } from "../actions";
import { ApplicationAssessmentActionType } from "../actions/applicationAssessment";
import { ApplicationAssessmentInterface } from "../shared/interfaces";

// default state

export const ApplicationAssessmentReducer = (
  state: ApplicationAssessmentInterface[] | null = null,
  action: ApplicationAssessmentActionType
) => {
  let index;
  switch (action.type) {
    case ActionTypes.GET_APPLICATIONS_TO_ASSESS:
      return action.payload;
    case ActionTypes.RE_ASSESS_APPLICATION:
      if (state) {
        index = state.findIndex(
          (application) =>
            application.application_id === action.payload.application_id
        ); //finding index of the item
        if (index >= 0) {
          state[index].application_status = action.payload.application_status;
        }
      }
      return state;
    default:
      return state;
  }
};
