import React, { useState } from 'react'
import { HiCheck } from 'react-icons/hi';
import { ImCheckboxChecked, ImCheckboxUnchecked } from 'react-icons/im';
import { MdModeEdit } from 'react-icons/md';
import { LanguageInterface, QuestionAnswerInterface, QuestionItemInterface, QuestionLanguages, questionTypeElementsType } from '../../../../../interfaces/question';
import { SelectedKeysEdit } from './EditQuestion';

interface QuestionAnswerProps {
  selectedQuestionType: questionTypeElementsType | null;
  selectedQuestionItem: QuestionItemInterface;
  wholeQuestion: QuestionItemInterface[];
  selectedLanguage: LanguageInterface;
  updateAnswerSingleMultiple: (question: QuestionItemInterface, editedAnswerIndex: string, editedAnswer: QuestionAnswerInterface) => void;
  addAnswerSingleMultiple: (question: QuestionItemInterface, addedAnswer: QuestionAnswerInterface) => void;
  removeAnswerSingleMultiple: (question: QuestionItemInterface[], removedAnswer: QuestionAnswerInterface) => void;
  selectedKey: SelectedKeysEdit | null;
  setSelectedKey: (selectedKey: SelectedKeysEdit | null) => void;
}

const QuestionAnswer = (props: QuestionAnswerProps) => {
  const [addedAnswer, setAddedAnswer] = useState<{lang: LanguageInterface; answer: QuestionAnswerInterface}[]>([]);
  const [showAddAnswerStatus, setShowAddAnswer] = useState<boolean>(false);
  const AddAnswer = () => {
    if (addedAnswer !== null) {
      if (addedAnswer.length !== QuestionLanguages.length) {
        alert("Please fill question answer!" + addedAnswer.length);
      } else {
        for (let answer of addedAnswer) {
          let selectedQL = props.wholeQuestion.find(itm => itm.language === answer.lang.key);
          if (selectedQL !== undefined) {
            props.addAnswerSingleMultiple(selectedQL, answer.answer);
          }
        }
        setAddedAnswer([]);
        setShowAddAnswer(false);
      }
    }
  }
  const handleAnswer = (lang: LanguageInterface, answer: QuestionAnswerInterface) => {
    if (addedAnswer.find(itm => itm.lang.key === lang.key) === undefined) {
      setAddedAnswer([...addedAnswer, { lang: lang, answer: answer }]);
    } else {
      setAddedAnswer([...addedAnswer.filter(itm => itm.lang.key !== lang.key), { lang: lang, answer: answer }]);
    }
  }

  const handleStatus = (status: boolean) => {
    if (addedAnswer.length > 0) {
      let backup = [];
      for (let itm of addedAnswer) {
        backup.push({
          lang: itm.lang,
          answer: { value: itm.answer.value, status: status }
        })
      }
      setAddedAnswer(backup);
    }
  }
  return (
    <div className="flex flex-col mb-3 text-sm gap-2 bg-white px-2 py-2 rounded">
      {showAddAnswerStatus === true && 
        <label className="text-gray-600 font-bold">Add question Answer</label>
      }
      
      {showAddAnswerStatus === false ?
      <div className="flex flex-row items-center gap-3 animate__animated animate__fadeIn animate__fast">
        <div onClick={() => setShowAddAnswer(true)} className="btn bg-blue-50 text-blue-600 cursor-pointer hover:bg-blue-200 hover:text-blue-800 border border-blue-200 w-32">Add answer</div>
        <div className="w-full flex flex-row items-center justify-between pr-2 py-1">
          <div className="text-gray-600 font-bold flex flex-row items-center">
            {props.selectedKey !== null && props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
            "Edit Question answers" : "Question answers"}
          </div>
          {props.selectedKey !== null && props.selectedKey === SelectedKeysEdit.QuestionAnswer &&
          <div onClick={() => props.setSelectedKey(null)} className="bg-blue-100 text-blue-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-200 hover:text-blue-700">
            <HiCheck className="text-3xl" />
          </div>}
          {props.selectedKey !== SelectedKeysEdit.QuestionAnswer &&
          <div onClick={() => props.setSelectedKey(SelectedKeysEdit.QuestionAnswer)} className="bg-gray-100 text-gray-600 rounded-full flex items-center justify-center h-12 w-12 cursor-pointer hover:bg-blue-100 hover:text-blue-600">
            <MdModeEdit className="text-3xl" />
          </div>}
        </div>
      </div>
      :
      <div className="mb-2 gap-3 bg-white rounded animate__animated animate__fadeIn animate__fast">
        <div className="flex flex-col gap-2">
        {QuestionLanguages.map((item, i) => (
          <div key={i + 1}className="flex flex-col gap-2">
            <span className="font-bold text-blue-600">{item.value}</span>
          <input 
            value={addedAnswer.length > 0 ? addedAnswer.find(itm => itm.lang.key === item.key)?.answer.value : ""}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => handleAnswer(item, { value: e.target.value, status: addedAnswer.length > 0 ? addedAnswer[0].answer.status : false})}
            type="text" className="input-gray-lg" />
          </div>
        ))}
        </div>
        <div className="flex flex-row mt-3 items-center justify-end gap-2">
        <div 
        onClick={() => handleStatus(addedAnswer.length > 0 ? !(addedAnswer[0].answer.status) : false)}
        className={`flex flex-row items-center gap-2 cursor-pointer ${addedAnswer.length > 0 && addedAnswer[0].answer.status === true ? "text-blue-600" : "text-gray-600"} hover:text-blue-600`}>
          {addedAnswer.length > 0 && addedAnswer[0].answer.status === false ?
          <ImCheckboxUnchecked className="text-2xl cursor-pointer" /> :
          <ImCheckboxChecked className="text-2xl cursor-pointer" />}
          <span className="font-bold text-xl">{addedAnswer.length > 0 && addedAnswer[0].answer.status === true ? "True" : "False"}</span>
        </div>
        <div onClick={() => AddAnswer()} className="btn bg-blue-600 text-white hover:bg-blue-800 cursor-pointer w-24 text-center">Add</div>
        <div onClick={() => setShowAddAnswer(false)} className="btn bg-gray-500 text-white hover:bg-gray-700 cursor-pointer w-24 text-center">Cancel</div>
      </div>
      </div>}
      {showAddAnswerStatus === false &&
      <div className="rounded bg-white px-2 py-2">{console.log(props.selectedQuestionType)}
        {props.selectedQuestionType === questionTypeElementsType.SingleMultiple && 
        props.selectedQuestionItem.answer.map((item, i) => (
          <div className="flex flex-row items-center justify-between mb-2 gap-3">
            {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
                if (check === undefined) {
                  check = props.wholeQuestion[0];
                }
                props.updateAnswerSingleMultiple(
                  check, item.value, {
                    value: e.target.value,
                    status: item.status
                  }
                )
              }}
              key={i + 1}
              value={`${item.value}`}
              type="text" 
              className="input-gray-lg w-full border-2 border-gray-500" /> :
              <div className="w-full border border-blue-100 px-3 py-2 rounded">{item.value}</div>}
              {props.selectedKey === SelectedKeysEdit.QuestionAnswer ?
              <div 
                onClick={() => {
                  let check = props.wholeQuestion.find(itm => itm.language === props.selectedLanguage.key);
                  if (check === undefined) {
                    check = props.wholeQuestion[0];
                  }
                  props.updateAnswerSingleMultiple(
                    check, item.value, {
                      value: item.value,
                      status: !item.status
                    }
                  )
                }}
                className={`flex flex-row items-center gap-2 cursor-pointer ${item.status === true ? "text-blue-600" : "text-gray-600"} hover:text-blue-600 w-40`}>
                  {item.status === false ?
                  <ImCheckboxUnchecked className="text-2xl cursor-pointer" /> :
                  <ImCheckboxChecked className="text-2xl cursor-pointer" />}
                  <span className="font-bold ">{item.status === true ? "Correct" : "Incorrect"}</span>
                </div> :
                <div className="w-28 flex justify-end text-center">
                  <span className={`font-bold px-3 py-2 rounded w-28 ${item.status === true ? "text-blue-600 bg-blue-50" : "text-gray-600 bg-gray-50"}`}>{item.status === true ? "Correct" : "Incorrect"}</span>
                </div>}
              <div 
              onClick={() => {
                props.removeAnswerSingleMultiple(props.wholeQuestion, item)
              }}
              className="btn bg-red-50 text-red-600 hover:bg-red-200 hover:text-red-800 cursor-pointer">Remove</div>
          </div>
        ))
        }
      </div>}
    </div>
  )
}

export default QuestionAnswer
