import axios from "axios";
import React, { useEffect, useState } from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank, MdDelete } from "react-icons/md";
import { connect } from "react-redux";
import { positionRequirementInterface } from ".";
import { PushPopupAlert } from "../../../actions";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../../actions/document";
import {
  getPositions,
  PositionCreateInterface,
} from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Loading from "../../../components/Loading/Loading";
import { course } from "../../../interfaces/course";
import { education_category } from "../../../interfaces/education_category";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { school_level } from "../../../interfaces/school_level";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";

interface StepThreeProps {
  positionId: string;
  positionTitle: string;
  positionDesc: string;
  thisFieldList: positionRequirementInterface[] | null;
  thisEdication: education_category;
  thisLevel: school_level;
  thisCategory: string;
  thisSubCategory: string;
  thisCoursesId: string[];
  thisRequirementList:
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
  };
  modalAction: false | "CREATE_POSITION" | "UPDATE_POSITION";
  setPositionId: React.Dispatch<React.SetStateAction<string>>;
  setPositionTitle: React.Dispatch<React.SetStateAction<string>>;
  setPositionDesc: React.Dispatch<React.SetStateAction<string>>;
  setThisCourses: React.Dispatch<React.SetStateAction<course[] | null>>;
  setThisCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisSubCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisFieldList: React.Dispatch<
    React.SetStateAction<positionRequirementInterface[] | null>
  >;
  setThisRequirementList: React.Dispatch<
    React.SetStateAction<
      | {
          user_detail_category_id: string;
          user_detail_subcategory_id: string;
          position_requirement: positionRequirementInterface[];
          required: boolean;
        }[]
      | null
    >
  >;
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
    }>
  >;
  setModalAction: React.Dispatch<
    React.SetStateAction<false | "CREATE_POSITION" | "UPDATE_POSITION">
  >;
  document: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getPositions: Function;
  cleanStates: () => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const StepThree = (props: StepThreeProps) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [thisField, setThisField] = useState<string>("");
  const [isFieldRequired, setIsFieldRequired] = useState<boolean>(false);
  const [isRequirementRequired, setIsRequirementRequired] =
    useState<boolean>(false);

  useEffect(() => {
    if (!props.document.documentCategory) {
      props.getDocumentCategory();
    } else if (!props.document.documentSubCategory) {
      props.getDocumentSubCategory();
    } else if (!props.document.documentFields) {
      props.getDocumentFields();
    } else {
      setLoading(false);
    }
    console.log({
      fieldS: props.thisFieldList,
      all_fields: props.document.documentFields,
    });
  }, [props]);

  const addField = () => {
    if (!thisField.length) {
      return setFormError({
        element: "position_field",
        msg: "Document field is required",
      });
    }

    if (props.thisFieldList) {
      let index: number = props.thisFieldList.findIndex(
        (field) => field.category_subcategory_data_id === thisField
      );
      if (index >= 0) {
        let newArray = [...props.thisFieldList];
        newArray[index] = {
          category_subcategory_data_id: thisField,
          required: isFieldRequired,
        };
        props.setThisFieldList(newArray);
      } else {
        props.setThisFieldList([
          ...props.thisFieldList,
          {
            category_subcategory_data_id: thisField,
            required: isFieldRequired,
          },
        ]);
      }
    } else {
      props.setThisFieldList([
        {
          category_subcategory_data_id: thisField,
          required: isFieldRequired,
        },
      ]);
    }

    setThisField("");
    setIsFieldRequired(false);
  };

  const removeField = (field_id: string) => {
    if (props.thisFieldList) {
      let newArray = [...props.thisFieldList];
      newArray = newArray.filter(
        (item) => item.category_subcategory_data_id !== field_id
      );
      props.setThisFieldList(newArray);
    }
  };

  const removeRequirement = (requirement_id: string) => {
    if (props.thisRequirementList) {
      let newArray = [...props.thisRequirementList];
      newArray = newArray.filter(
        (item) => item.user_detail_category_id !== requirement_id
      );
      props.setThisRequirementList(newArray);
    }
  };

  const addRequirement = () => {
    if (thisField.length > 0) {
      return setFormError({
        element: "position_field",
        msg: "Save Document field first",
      });
    }
    if (!props.positionTitle.length) {
      return setFormError({
        element: "position_title",
        msg: "Position title is required",
      });
    }

    if (!props.positionDesc.length) {
      return setFormError({
        element: "position_desc",
        msg: "Position Description is required",
      });
    }

    if (!props.thisCategory) {
      return setFormError({
        element: "position_cat",
        msg: "Document category is required",
      });
    }

    if (!props.thisFieldList) {
      return setFormError({
        element: "position_field",
        msg: "Document field is required",
      });
    }

    if (props.thisRequirementList) {
      let index: number = props.thisRequirementList.findIndex(
        (item) => item.user_detail_category_id === props.thisCategory
      );
      if (index >= 0) {
        let newArray = [...props.thisRequirementList];
        newArray[index].user_detail_category_id = props.thisCategory;
        newArray[index].user_detail_subcategory_id = props.thisSubCategory
          ? props.thisSubCategory
          : "";
        newArray[index].position_requirement = props.thisFieldList;
        newArray[index].required = isRequirementRequired;
        props.setThisRequirementList(newArray);
      } else {
        props.setThisRequirementList([
          ...props.thisRequirementList,
          {
            user_detail_category_id: props.thisCategory,
            user_detail_subcategory_id: props.thisSubCategory
              ? props.thisSubCategory
              : "",
            position_requirement: props.thisFieldList,
            required: isRequirementRequired,
          },
        ]);
      }
    } else {
      props.setThisRequirementList([
        {
          user_detail_category_id: props.thisCategory,
          user_detail_subcategory_id: props.thisSubCategory
            ? props.thisSubCategory
            : "",
          position_requirement: props.thisFieldList,
          required: isRequirementRequired,
        },
      ]);
    }

    props.setThisCategory("");
    setThisField("");
    setIsRequirementRequired(false);
    setIsFieldRequired(false);
  };

  const handleSubmit = async () => {
    // validation
    if (!props.positionTitle.length) {
      return setFormError({
        element: "position_title",
        msg: "Position title is required",
      });
    } else if (!props.positionDesc.length) {
      return setFormError({
        element: "position_desc",
        msg: "Position Description is required",
      });
    } else if (
      !props.thisRequirementList ||
      !props.thisRequirementList.length
    ) {
      return setFormError({
        element: "position_cat",
        msg: "Please fill the position requirement.",
      });
    } else {
      setLoadingRequest(true);

      try {
        // Update current
        if (props.modalAction === "UPDATE_POSITION") {
          const api_body: PositionCreateInterface = {
            position_id: props.positionId,
            position_title: props.positionTitle,
            description: props.positionDesc,
            school_level_id: props.thisLevel.school_level_id,
            education_institute_id: props.thisEdication.education_institute_id,
            courses: props.thisCoursesId,
            requirements: props.thisRequirementList,
          };
          console.log({
            create_position: api_body,
          });
          const res = await axios.patch(
            `${API.APPLICATION}/position`,
            api_body
          );

          //   props.updateAplicationSession(res.data.data);
          props.getPositions((status: boolean, msg: string) => {
            setLoadingRequest(status);
            if (msg.length) {
              props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
          });

          props.PushPopupAlert({
            title: `Position updated successfully`,
            theme: PopupAlertTheme.info,
          });
          //   props.setSelectedPeriod(null);
          //   props.closeModal(false);
          props.cleanStates();
          props.setModalAction(false);
          console.log("Result: ", res.data.data);
        } else {
          const api_body: PositionCreateInterface = {
            position_title: props.positionTitle,
            description: props.positionDesc,
            school_level_id: props.thisLevel.school_level_id,
            education_institute_id: props.thisEdication.education_institute_id,
            courses: props.thisCoursesId,
            requirements: props.thisRequirementList,
          };
          console.log({
            create_position: api_body,
          });
          // Add new
          const res = await axios.post(`${API.APPLICATION}/position`, api_body);
          //   props.createAplicationSession(res.data.session);
          props.getPositions((status: boolean, msg: string) => {
            setLoadingRequest(status);
            if (msg.length) {
              props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
          });
          props.PushPopupAlert({
            title: `Position created successfully`,
            theme: PopupAlertTheme.success,
          });
          //   props.closeModal(false);
          props.cleanStates();
          console.log("CREATING_NEW_POSITION: ", res);
        }
      } catch (error) {
        props.PushPopupAlert({
          title: `${errorToText(error)}! Try again`,
          theme: PopupAlertTheme.danger,
        });
        console.error("Err: ", { error });
        setLoadingRequest(false);
      }
    }
  };

  if (loading) {
    return <Loading />;
  }
  return (
    <section className="mx-4">
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Position title
        </span>
        <input
          disabled={loadingRequest}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            props.setPositionTitle(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={props.positionTitle}
          className={`input-white border-2 ${
            formError !== null && formError.element === "position_title"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "position_title" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      <div className="flex flex-col mb-2">
        <span className="text-sm text-gray-600 font-bold mb-1">
          Position description
        </span>
        <input
          disabled={loadingRequest}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            props.setPositionDesc(e.target.value);
            setFormError(null);
          }}
          type="text"
          value={props.positionDesc}
          className={`input-white border-2 ${
            formError !== null && formError.element === "position_desc"
              ? "border-red-500"
              : "border-gray-500"
          }`}
        />
        {formError !== null && formError.element === "position_desc" && (
          <span className="text-red-600 font-bold text-xs">
            {formError.msg}
          </span>
        )}
      </div>
      {/* ==================== Position requirements ======================== */}
      <div className="bg-gray-100 p-3 border-2 rounded mb-3">
        <span className="text-xl text-gray-600 font-bold mb-1">
          Position requirements
        </span>
        {/* DOCUMENT CATEGORY */}
        <div className="flex flex-col mb-2">
          <div className="text-sm text-gray-600 font-bold mb-1">
            Document category
          </div>
          <select
            className={`block w-full bg-white text-grey-darker border-2 rounded h-10 px-3 ${
              formError !== null && formError.element === "position_cat"
                ? "border-red-500"
                : "border-gray-500"
            }`}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
              props.setThisCategory(e.target.value)
            }
            value={props.thisCategory ? props.thisCategory : ""}
          >
            <option value="">Choose Document Category</option>
            {props.document.documentCategory &&
            props.document.documentCategory.length > 0 ? (
              props.document.documentCategory.map((item, index) => (
                <option
                  value={item.user_detail_category_id}
                  key={index + item.user_detail_category_id}
                >
                  {item.title}
                </option>
              ))
            ) : (
              <option value="">No Document Category found</option>
            )}
          </select>
          {formError !== null && formError.element === "position_cat" && (
            <span className="text-red-600 font-bold text-xs">
              {formError.msg}
            </span>
          )}
        </div>

        {/* DOCUMENT SUB CATEGORY */}
        {props.thisCategory.length > 0 && (
          <div className="flex flex-col mb-2 animate__animated animate__fadeIn">
            <div className="text-sm text-gray-600 font-bold mb-1">
              Sub Document category
            </div>
            <select
              className={`block w-full bg-white text-grey-darker border-2 rounded h-10 px-3 ${
                formError !== null && formError.element === "position_sub"
                  ? "border-red-500"
                  : "border-gray-500"
              }`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                props.setThisSubCategory(e.target.value)
              }
              value={props.thisSubCategory ? props.thisSubCategory : ""}
            >
              <option value="">Choose Sub-Document Category</option>
              {props.document.documentSubCategory &&
              props.document.documentSubCategory.filter(
                (item) => item.category_id === props.thisCategory
              ).length > 0 ? (
                props.document.documentSubCategory
                  .filter((item) => item.category_id === props.thisCategory)
                  .map((item, index) => (
                    <option
                      value={item.user_detail_subcategory_id}
                      key={index + item.user_detail_subcategory_id}
                    >
                      {item.title}
                    </option>
                  ))
              ) : (
                <option value="">No Sub-Document Category found</option>
              )}
            </select>
            {formError !== null && formError.element === "position_sub" && (
              <span className="text-red-600 font-bold text-xs">
                {formError.msg}
              </span>
            )}
          </div>
        )}

        {/* DOCUMENT FIELD */}
        {props.thisCategory.length > 0 && (
          <div className="flex flex-col mb-2 animate__animated animate__fadeIn">
            <div className="text-sm text-gray-600 font-bold mb-1">
              Documment fields
            </div>
            <div className="flex items-center gap-2">
              <select
                className={`block w-full bg-white text-grey-darker border-2 rounded h-10 px-3 ${
                  formError !== null && formError.element === "position_field"
                    ? "border-red-500"
                    : "border-gray-500"
                }`}
                onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                  setThisField(e.target.value);
                  setFormError(null);
                }}
                value={thisField ? thisField : ""}
              >
                <option value="">Choose Document Field</option>
                {props.document.documentFields &&
                props.document.documentFields.filter(
                  (item) => item.category_id === props.thisCategory
                ).length > 0 ? (
                  props.document.documentFields
                    .filter((item) => item.category_id === props.thisCategory)
                    .map((item, index) => (
                      <option value={item._id} key={index + item._id}>
                        {item.title}
                      </option>
                    ))
                ) : (
                  <option value="">No Document Field found</option>
                )}
              </select>
              <div
                className="flex flex-row items-center text-lg gap-1 animate__animated animate__fadeIn cursor-pointer"
                onClick={() => !loading && setIsFieldRequired(!isFieldRequired)}
              >
                <div className="text-gray-600 font-bold">Required</div>
                {isFieldRequired ? (
                  <MdCheckBox className="w-6 h-6 text-blue-700" />
                ) : (
                  <MdCheckBoxOutlineBlank className="w-6 h-6" />
                )}
              </div>
              <button
                className="btn btn-blue"
                onClick={() => {
                  addField();
                  setFormError(null);
                }}
                disabled={loadingRequest}
              >
                Add
              </button>
            </div>
            {formError !== null && formError.element === "position_field" && (
              <span className="text-red-600 font-bold text-xs">
                {formError.msg}
              </span>
            )}

            {/* ============================== DISPLAYING SELECTED FIELDS ================================= */}
            {props.thisFieldList && props.thisFieldList.length > 0 && (
              <section className="bg-white border-2 border-gray-300 rounded p-2 my-2">
                <span className="text-lg font-semibold">Document fields:</span>
                {props.thisFieldList.map((item, index) => (
                  <div
                    key={index + item.category_subcategory_data_id}
                    className={`border flex flex-row px-3 py-2 rounded items-start my-1 justify-between hover:bg-gray-50 cursor-pointer`}
                  >
                    <div
                      className="flex flex-col"
                      onClick={() =>
                        !loading &&
                        (setThisField(item.category_subcategory_data_id),
                        setIsFieldRequired(item.required))
                      }
                    >
                      <span className="text-gray-600 font-bold">
                        {props.document.documentFields &&
                          props.document.documentFields.find(
                            (field) =>
                              field._id === item.category_subcategory_data_id
                          )?.title}
                      </span>
                      <span className={`text-xs pr-5 text-gray-500`}>
                        {item.required ? "Required" : "Optional"}
                      </span>
                    </div>

                    <div
                      onClick={() => {
                        !loading &&
                          removeField(item.category_subcategory_data_id);
                      }}
                      className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 ${
                        loading ? "cursor-not-allowed" : "cursor-pointer"
                      }`}
                    >
                      <MdDelete className="text-3xl text-red-700" />
                    </div>
                  </div>
                ))}
              </section>
            )}
            {/* ============================ END DISPLAYING SELECTED FIELDS ============================== */}
          </div>
        )}

        {/* DOCUMENT REQUIREMENT FOOTER */}
        {props.thisCategory.length > 0 && (
          <div className="flex items-center justify-between">
            <div
              className="flex flex-row items-center text-lg gap-1 animate__animated animate__fadeIn cursor-pointer"
              onClick={() =>
                !loading && setIsRequirementRequired(!isRequirementRequired)
              }
            >
              <div className="text-gray-600 font-bold">Required</div>
              {isRequirementRequired ? (
                <MdCheckBox className="w-6 h-6 text-blue-700" />
              ) : (
                <MdCheckBoxOutlineBlank className="w-6 h-6" />
              )}
            </div>

            <button
              className="btn btn-blue mb-2 animate__animated animate__fadeIn focus:outline-none"
              onClick={() => addRequirement()}
              disabled={loadingRequest}
            >
              Add Requirement
            </button>
          </div>
        )}

        {/* ============================== DISPLAYING SELECTED REQUIREMENTS ================================= */}
        {props.thisRequirementList && props.thisRequirementList.length > 0 && (
          <section className="bg-white border-2 border-gray-300 rounded p-2 my-2">
            <span className="text-lg font-semibold">
              Position Requirements:
            </span>
            {props.thisRequirementList.map((item, index) => (
              <div
                key={index + item.user_detail_category_id}
                className={`border flex flex-row px-3 py-2 rounded items-start my-1 justify-between hover:bg-gray-50 cursor-pointer`}
              >
                <div
                  className="flex flex-col"
                  onClick={() =>
                    !loading &&
                    (props.setThisFieldList(item.position_requirement),
                    props.setThisCategory(item.user_detail_category_id),
                    props.setThisSubCategory(item.user_detail_subcategory_id),
                    setIsRequirementRequired(item.required))
                  }
                >
                  <span className="text-gray-600 font-bold">
                    {props.document.documentCategory &&
                      props.document.documentCategory.find(
                        (field) =>
                          field.user_detail_category_id ===
                          item.user_detail_category_id
                      )?.title}
                  </span>
                  <span className={`text-xs pr-5 text-gray-500`}>
                    {item.required ? "Required" : "Optional"}
                  </span>
                </div>

                <div
                  onClick={() => {
                    !loading && removeRequirement(item.user_detail_category_id);
                  }}
                  className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 ${
                    loading ? "cursor-not-allowed" : "cursor-pointer"
                  }`}
                >
                  <MdDelete className="text-3xl text-red-700" />
                </div>
              </div>
            ))}
          </section>
        )}
        {/* ============================ END DISPLAYING SELECTED REQUIREMENTS ============================== */}
      </div>
      <div className="flex items-center justify-between">
        <button
          className="btn bg-gray-500 text-gray-50 mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() =>
            props.setFormSteps({ ONE: false, TWO: true, THREE: false })
          }
        >
          Back
        </button>
        <button
          className="btn btn-blue mb-2 animate__animated animate__fadeIn focus:outline-none"
          onClick={() => handleSubmit()}
          disabled={loadingRequest}
        >
          {loadingRequest ? "Loading..." : "Save"}
        </button>
      </div>
    </section>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return {
    document,
  };
};

export default connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  getPositions,
  PushPopupAlert,
})(StepThree);
