import { ActionTypes } from "../actions";
import { SystemActionType } from "../actions/settings";
import { PositionAPI } from "../interfaces/position";
import {
  ApplicationSessionInterface,
  SystemSettingsInterface,
} from "../shared/interfaces";

export interface SystemSettingsType {
  systemSettings: SystemSettingsInterface[];
  applicationSessions: ApplicationSessionInterface[];
  systemPositions: PositionAPI[];
}

// default state
const initialState = {
  systemSettings: [],
  applicationSessions: [],
  systemPositions: [],
};

export const systemSettingsReducer = (
  state: SystemSettingsType = initialState,
  action: SystemActionType
) => {
  let index: number = -1;
  let data_SETTINGS: SystemSettingsInterface[] = [];
  let _data_APPLICATION: ApplicationSessionInterface[] = [];
  let _data_POSITION: PositionAPI[] = [];
  switch (action.type) {
    case ActionTypes.GET_SYSTEM_SETTINGS:
      return {
        ...state,
        systemSettings: action.payload,
      };
    case ActionTypes.DISABLE_SYSTEM_SETTINGS:
      data_SETTINGS = [...state.systemSettings];
      index = state.systemSettings.findIndex(
        (item) => item.system_setting_id === action.payload.system_setting_id
      );
      if (index >= 0) {
        data_SETTINGS[index].status = action.payload.status;
      }
      return {
        ...state,
        systemSettings: data_SETTINGS,
      };
    case ActionTypes.GET_APPLICATION_SESSIONS:
      return {
        ...state,
        applicationSessions: action.payload,
      };
    case ActionTypes.CREATE_APPLICATION_SESSIONS:
      return {
        ...state,
        applicationSessions: [...state.applicationSessions, action.payload],
      };
    case ActionTypes.UPDATE_APPLICATION_SESSIONS:
      _data_APPLICATION = [...state.applicationSessions];
      _data_APPLICATION.forEach((item, index) => {
        console.log(index); // 0, 1, 2
        console.log(item); // 9, 2, 5
        if (
          _data_APPLICATION[index].application_session_id ===
          action.payload.application_session_id
        ) {
          _data_APPLICATION[index].application_session_name =
            action.payload.application_session_name;
          _data_APPLICATION[index].period = action.payload.period;
          _data_APPLICATION[index].application_start_date =
            action.payload.application_start_date;
          _data_APPLICATION[index].application_end_date =
            action.payload.application_end_date;
          _data_APPLICATION[index].is_active = action.payload.is_active;
        } else {
          if (action.payload.is_active) {
            _data_APPLICATION[index].is_active = false;
          }
        }
      });
      // if (index >= 0) {
      //   _data[index].application_session_name =
      //     action.payload.application_session_name;
      //   _data[index].period = action.payload.period;
      //   _data[index].application_start_date =
      //     action.payload.application_start_date;
      //   _data[index].application_end_date = action.payload.application_end_date;
      //   _data[index].is_active = action.payload.is_active;
      // }
      return {
        ...state,
        applicationSessions: _data_APPLICATION,
      };
    case ActionTypes.DISABLE_APPLICATION_SESSION:
      _data_APPLICATION = [...state.applicationSessions];
      index = -1;
      index = _data_APPLICATION.findIndex(
        (item) =>
          item.application_session_id === action.payload.application_session_id
      );
      if (index >= 0) {
        _data_APPLICATION[index].is_active = action.payload.status;
      }
      return {
        ...state,
        applicationSessions: _data_APPLICATION,
      };
    case ActionTypes.GET_POSTIONS:
      return {
        ...state,
        systemPositions: action.payload,
      };
    // case ActionTypes.CREATE_POSITION:
    //   return {
    //     ...state,
    //     systemPositions: [...state.systemPositions, action.payload],
    //   };
    // case ActionTypes.UPDATE_POSITION:
    //   _data_POSITION = [...state.systemPositions];
    //   index = _data_POSITION.findIndex(
    //     (position) => position.position_id === action.payload.position_id
    //   ); //finding index of the item
    //   if (index >= 0) {
    //     _data_POSITION[index].position_title = action.payload.position_title;
    //     _data_POSITION[index].description = action.payload.description;
    //   }
    //   return _data_POSITION;
    case ActionTypes.DELETE_POSITION:
      return {
        ...state,
        systemPositions: state.systemPositions.filter(
          (element) => element.position_id !== action.payload
        ),
      };
    default:
      return state;
  }
};
